//引入immutable使用fromJS将js对象转化成immutable对象
import { fromJS } from "immutable";
import * as actionType from "./actiontype";
const defauItState = fromJS({
  toppicList: [],
  acticleList: [],
  recommendList: [],
  writerlist: [],
  writerpage: 1,
  totalpage: 1,
  acticlepage: 1,
  showScroll: false
});
//导出一个纯函数
export default (state = defauItState, action) => {
  //immutable对象的set方法，会结合之前immutable对象的值
  //和现在设置的值，返回一个全新的对象
  switch (action.type) {
    case actionType.change_home_data:
      return state.merge({
        toppicList: fromJS(action.toppicList),
        acticleList: fromJS(action.acticleList),
        recommendList: fromJS(action.recommendList),
        writerlist: fromJS(action.writerlist),
        totalpage: fromJS(action.totalpage)
      });
    case actionType.change_page:
      return state.set("writerpage", action.page);
    case actionType.addhomelist:
      return state.merge({
        acticleList: state.get("acticleList").concat(action.list),
        acticlepage: action.nextpage
      });
    case actionType.getScrolltop: return(
      state.set('showScroll',action.buer)
      )
    default:
      return state;
  }
};
