import React, { PureComponent } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { LoginWrapper, LoginBox, Input, Btn } from "./style";
import { actionCreators } from "./store";
class Login extends PureComponent {
  render() {
    const { loginstate } = this.props;

    if (!loginstate) {
      return (
        <LoginWrapper>
          <LoginBox>
            <Input
              placeholder="账号"
              ref={input => {
                this.account = input;
              }}
            ></Input>
            <Input
              placeholder="密码"
              ref={input => {
                this.pwd = input;
              }}
              type="password"
            ></Input>
            <Btn
              onClick={() => {
                this.props.login(this.account, this.pwd);
              }}
            >
              登录
            </Btn>
          </LoginBox>
        </LoginWrapper>
      );
    } else {
      return <Redirect to="/" />;
    }
  }
}
const mapState = state => ({
  loginstate: state.getIn(["login", "login"])
});
const mapdispath = dispath => ({
  login(account, pwd) {
    dispath(actionCreators.login(account.value, pwd.value));
  }
});
export default connect(mapState, mapdispath)(Login);
