//引入immutable使用fromJS将js对象转化成immutable对象
import { fromJS } from "immutable";
import * as actionType from "./actionType";
const defauItState = fromJS({
  login: false,
  name: ""
});
//导出一个纯函数
export default (state = defauItState, action) => {
  //immutable对象的set方法，会结合之前immutable对象的值
  //和现在设置的值，返回一个全新的对象
  switch (action.type) {
    case actionType.changeLogin:
      return state.merge({
        login: action.value,
        name: action.name
      });
    case actionType.LoginOut:
      return state.merge({
        login: action.value
      });
    default:
      return state;
  }
};
