import styled from "styled-components";
export const HomeWrapper = styled.div`
  width: 960px;
  margin: 0 auto;
  overflow: hidden;
`;
export const HomeLeft = styled.div`
  margin-left: 15px;
  padding-top: 30px;
  width: 625px;
  float: left;
`;
export const HomeRight = styled.div`
  width: 280px;
  float: right;
`;
//Toppic组件css样式
export const ToppicWrapper = styled.div`
  padding: 20px 0 10px 0;
  overflow: hidden;
  margin-left: -18px;
  border-bottom: 1px solid #dcdcdc;
`;
export const ToppicItem = styled.div`
  float: left;
  background: #f7f7f7;
  height: 32px;
  line-height: 32px;
  margin-left: 18px;
  margin-bottom: 18px;
  font-size: 14px;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  padding-right: 10px;
  img {
    width: 32px;
    float: left;
    margin-right: 10px;
  }
`;
//List组件css样式

export const ListItem = styled.div`
  padding: 20px 0;
  border-bottom: 1px solid #dcdcdc;
  overflow: hidden;
  img {
    width: 125px;
    height: 100px;
    float: right;
    border-radius: 10px;
  }
`;
export const Listinfo = styled.div`
  width: 500px;
  float: left;
  .title {
    font-size: 18px;
    line-height: 27px;
    font-weight: bold;
    color: #333;
    margin-bottom: 4px;
  }
  .desc {
    line-height: 24px;
    font-size: 13px;
    color: #999;
  }
`;
export const LoadMore = styled.div`
  width: 100%;
  height: 40px;
  margin: 30px 0;
  line-height: 40px;
  background: #a5a5a5;
  text-align: center;
  border-radius: 20px;
  color: #fff;
  cursor: pointer;
`;
//Recommend组件css样式
export const RecommendWrapper = styled.div`
  margin: 30px 0;
  width: 280px;
`;
export const RecommendItem = styled.div`
  width: 280px;
  height: 50px;
  background: url(${props => props.imgUrl});
  background-size: contain;
`;
//Writer组件css样式
export const WriterWrapper = styled.div`
  width: 280px;
`;
export const WriterInfotitle = styled.div`
  overflow: hidden;
  color: #969696;
`;
export const WriterInfoSwitch = styled.span`
  float: right;
  margin-bottom: 20px;
  .iconfont {
    float:left;
    transition: all 0.2s ease-in;
    transform-origin: center center;
  }
`;
export const WriterInfo = styled.div`
  width: 280px;
  margin-bottom: 15px;
  overflow: hidden;
`;
export const WriterInfopic = styled.div`
  width: 48px;
  height: 48px;
  float: left;
  margin-right: 10px;
  img {
    width: 48px;
    height: 48px;
    border-radius: 24px;
  }
`;
export const WriterInfoName = styled.a`
  line-height: 48px;
  margin-left: 15px;
`;
export const WriterInfoOther = styled.span`
  line-height: 48px;
  float: right;
  color: #42c02e;
  font-weight: bold;
`;
export const Backtop = styled.div`
position:fixed;
right:100px;
bottom:30px;
width:60px;
height:60px;
line-height:60px;
text-align:center;
font-size:14px;
border 1px solid #ccc;
`;
