import React from "react";
import { Provider } from "react-redux";
import { BrowserRouter, Route } from "react-router-dom";
import Header from "./common/header";
import Home from "./pages/home";
import Detail from "./pages/detail/loadable";
import Login from "./pages/login";
// import Write from "./pages/writer";
import Write from './pages/writer'
//引入store仓库
import store from "./store";
function App() {
  return (
    //Provider里面的组件都能使用store里的数据
    //Provider把store提供给内部组件
    <Provider store={store}>
      <BrowserRouter>
        <Header />
        <Route path="/react/write" exact component={Write}></Route>
        <Route path="/react" exact component={Home}></Route>
        <Route path="/react/login" exact component={Login}></Route>
        <Route path="/react/detail/:id" exact component={Detail}></Route>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
