import axios from "axios";
import * as actionType from "./actiontype";
import { fromJS } from "immutable";
const changeHomeDada = (result) => ({
  type: actionType.change_home_data,
  toppicList: result.toppicList,
  acticleList: result.acticleList,
  recommendList: result.recommendList,
  writerlist: result.writerlist,
  totalpage: Math.ceil(result.writerlist.length / 5),
});
export const getHomeInfo = () => {
  return (dispatch) => {
    axios
      .get("https://chenwenkai8023ni.github.io/react/api/home.json")
      .then((res) => {
        const result = res.data.data;
        dispatch(changeHomeDada(result));
      });
  };
};
export const Changepage = (page) => ({
  type: actionType.change_page,
  page,
});
const addHomeDada = (list, nextpage) => ({
  type: actionType.addhomelist,
  list: fromJS(list),
  nextpage,
});
export const getmorelist = (page) => {
  return (dispatch) => {
    axios
      .get(
        "https://chenwenkai8023ni.github.io/react/api/homelist.json?page=" +
          page
      )
      .then((res) => {
        const result = res.data.data;
        dispatch(addHomeDada(result, page + 1));
      });
  };
};
export const toggleTopshow = (buer) => ({
  type: actionType.getScrolltop,
  buer,
});
