import axios from "axios";
import * as actionType from "./actionType";
export const login = (accout, pwd) => {
  return (dispath) => {
    axios
      .get(
        "https://chenwenkai8023ni.github.io/react/api/login.json?account=" +
          accout +
          "&pwd=" +
          pwd
      )
      .then((res) => {
        const result = res.data.data;
        const name = res.data.name;
        if (result) {
          dispath(changeLogin(name));
        } else {
          alert("登录失败");
        }
      });
  };
};
export const loginOut = () => ({
  type: actionType.LoginOut,
  value: false,
});
const changeLogin = (name) => ({
  type: actionType.changeLogin,
  value: true,
  name,
});
