import styled from "styled-components";
export const LoginWrapper = styled.div`
  z-index: 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 56px;
  background: #eee;
  width: 100;
`;
export const LoginBox = styled.div`
  width: 400px;
  height: 220px;
  margin: 80px auto;
  background: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  padding-top: 30px;
  border: 1px solid #ccc;
`;
export const Input = styled.input`
  display: block;
  width: 200px;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  margin: 10px auto;
  color: #777;
`;
export const Btn = styled.div`
  width: 200px;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  color: #fff;
  background: #3194d0;
  border-radius: 15px;
  margin: 10px auto;
  text-align:center
`;
