import styled from "styled-components";
import Logopic from "../../station/logo.png";
export const HeaderWrapper = styled.div`
  width: 100%;
  height: 56px;
  border-bottom: 1px solid #f0f0f0;
`;
export const HeaderNav = styled.div`
  position: relative;
  z-index: 1;

  width: 1440px;
  height: 100%;
  margin: 0 auto;
`;
export const Logo = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 56px;
  width: 100px;
  background: url(${Logopic});
  background-size: contain;
`;
export const Nav = styled.div`
  width: 960px;
  height: 100%;
  padding-right: 70px;
  box-sizing: border-box;
  margin: 0 auto;
`;
export const NavItem = styled.div`
  line-height: 56px;
  padding: 0 15px;
  font-size: 17px;
  color: #333;
  cursor: pointer;
  &.left {
    float: left;
  }
  &.right {
    color: #969696;
    float: right;
  }
  &.action {
    color: #ea6f5a;
  }
`;
export const SearchInfo = styled.div`
  position: absolute;
  left: 20px;
  top: 56px;
  width: 220px;
  box-sizing: border-box;
  padding: 0 20px;
  background: white;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
`;
export const SearchInfoTitle = styled.div`
  margin-top: 18px;
  margin-bottom: 18px;
  color: #969696;
  fons-size: 14px;
  line-height: 20px;
`;
export const SearchInfoSwitch = styled.span`
  float: right;
  font-size: 13px;
  cursor: pointer;
  .iconfont {
    display: block;
    float: left;
    font-size: 12px;
    margin-right: 2px;
    transition: all 0.2s ease-in;
    transform-origin: center center;
  }
`;
export const SearchInfoItem = styled.a`
  display: block;
  float: left;
  margin-right: 10px;
  margin-bottom: 15px;
  padding: 0 5px;
  line-height: 20px;
  font-size: 14px;
  color: #666;
  border: 1px solid #ddd;
  border-radius: 3px;
`;
export const SearchWrapper = styled.div`
  position: relative;
  height: 100%;
  float: left;
  .zoom {
    position: absolute;
    background: #eee;
    z-index: 2;
    right: 0;
    bottom: 10px;
    border-radius: 50%;
    padding: 10px;
    &.focused {
      background: #777;
    }
  }
`;
export const NavSearch = styled.input.attrs({ placeholder: "搜索" })`
  width: 160px;
  height: 38px;
  outline: none;
  border: none;
  box-sizing: border-box;
  border-radius: 19px;
  margin-top: 9px;
  margin-left: 20px;
  padding: 0 0 0 20px;
  background: #eee;
  font-size: 14px;
  color: #666;
  &.slide-enter {
    transition: all 0.2s ease-out;
  }
  &.slide-enter-active {
    width: 220px;
  }
  &.slide-exit {
    transition: all 0.2s ease-out;
  }
  &.slide-exit-active {
    width: 160px;
  }
  &::placeholder {
    color: #999;
  }
  &.focused {
    width: 220px;
  }
`;
export const Addition = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  height: 56px;
`;
export const Btn = styled.div`
  line-height: 38px;
  border-radius: 19px;
  margin-top: 9px;
  border: 1px solid #ec6149;
  margin-right: 20px;
  padding: 0 20px;
  &.reg {
    float: left;
    color: #ec6149;
  }
  &.write {
    float: right;
    color: white;
    background: #ec6149;
    .iconfont {
      margin-right: 5px;
      line-height: 40 px;
    }
  }
`;
