import * as actionType from "./actiontype";
//引入immutable使用fromJS将js对象转化成immutable对象
import { fromJS } from "immutable";
const defauItState = fromJS({
  focused: false,
  mouseIn: false,
  list: [],
  page: 1,
  totalpage: 1
});
//导出一个纯函数
export default (state = defauItState, action) => {
  //immutable对象的set方法，会结合之前immutable对象的值
  //和现在设置的值，返回一个全新的对象
  switch (action.type) {
    case actionType.search_focus:
      return state.set("focused", true);
    case actionType.search_blur:
      return state.set("focused", false);
    case actionType.change_list:
      return state.merge({
        list: action.data,
        totalpage: action.totalpage
      });
    case actionType.mouseEnter:
      return state.set("mouseIn", true);
    case actionType.mouseLeave:
      return state.set("mouseIn", false);
    case actionType.Changepage:
      return state.set("page", action.page);
    default:
      return state;
  }
};
