import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { actionCreators } from "../store";

import {
  WriterWrapper,
  WriterInfotitle,
  WriterInfoSwitch,
  WriterInfopic,
  WriterInfo,
  WriterInfoName,
  WriterInfoOther
} from "../style";
class Writer extends PureComponent {
  getlist() {
    const { list, page, totalpage, writerChangepage } = this.props;
    const writerlist = [];
    const newlist = list.toJS();

    if (newlist.length) {
      for (let i = (page - 1) * 5; i < page * 5; i++) {
        writerlist.push(
          <WriterInfo key={newlist[i]._id}>
            <WriterInfopic>
              <img src={newlist[i].imgUrl} alt="" />
            </WriterInfopic>
            <WriterInfoName>{newlist[i].writername}</WriterInfoName>
            <WriterInfoOther>＋关注</WriterInfoOther>
          </WriterInfo>
        );
      }
    }
    return (
      <>
        <WriterInfotitle>
          推荐作者
          <WriterInfoSwitch
            onClick={() => {
              writerChangepage(page, totalpage, this.spinIcon);
            }}
          >
            <span
              ref={icon => {
                this.spinIcon = icon;
              }}
              className="iconfont"
            >
              &#xe66d;
            </span>
            换一换
          </WriterInfoSwitch>
        </WriterInfotitle>
        {writerlist}
      </>
    );
  }
  render() {
    return <WriterWrapper>{this.getlist()}</WriterWrapper>;
  }
}
const mapstate = state => ({
  list: state.getIn(["home", "writerlist"]),
  page: state.getIn(["home", "writerpage"]),
  totalpage: state.getIn(["home", "totalpage"])
});
const mapdispath = dispatch => ({
  writerChangepage(page, totalpage, spin) {
    console.log(page, totalpage, spin);
    let orgin = spin.style.transform.replace(/[^0-9]/gi, "");
    if (orgin) {
      orgin = parseInt(orgin, 10);
    } else {
      orgin = 0;
    }
    spin.style.transform = "rotate(" + (orgin + 360) + "deg)";
    if (page < totalpage) {
      dispatch(actionCreators.Changepage(page + 1));
    } else {
      dispatch(actionCreators.Changepage(1));
    }
  }
});
export default connect(mapstate, mapdispath)(Writer);
