import React, { PureComponent } from "react";
import {Link} from 'react-router-dom';
//使用connect方法与store数据做链接
import { connect } from "react-redux";
import { ListItem, Listinfo, LoadMore } from "../style";
import { actionCreators } from "../store";

class List extends PureComponent {
  render() {
    const { acticleList, goLoadMore, acticlepage } = this.props;
    return (
      <div>
        {acticleList.map(item => {
          return (
            <Link key={item.get("_id")} to={'/detail/'+item.get('_id')}>
              <ListItem>
                <img src={item.get("imgUrl")} alt="" />
                <Listinfo>
                  <h3 className="title">{item.get("title")}</h3>
                  <p className="desc">{item.get("desc")}</p>
                </Listinfo>
              </ListItem>
            </Link>
          );
        })}
        <LoadMore
          onClick={() => {
            goLoadMore(acticlepage);
          }}
        >
          跟多文章
        </LoadMore>
      </div>
    );
  }
}
const mapState = state => ({
  //acticleList:state.getIn(['home','acticleList'])
  acticleList: state.get("home").get("acticleList"),
  acticlepage: state.getIn(["home", "acticlepage"])
});
const mapdispath = dispath => ({
  goLoadMore(page) {
    dispath(actionCreators.getmorelist(page));
  }
});
export default connect(mapState, mapdispath)(List);
