import React, { PureComponent } from "react";
import Tocpic from "./components/Toppic";
import List from "./components/List";
import Recommend from "./components/Recommend";
import Writer from "./components/Writer";
import { connect } from "react-redux";
import { HomeWrapper, HomeLeft, HomeRight, Backtop } from "./style";
import { actionCreators } from "./store";
class Home extends PureComponent {
  handletop() {
    window.scrollTo(0, 0);
  }
  render() {  
    return (
      <HomeWrapper>
        <HomeLeft>
          <Tocpic />
          <List />
        </HomeLeft>
        <HomeRight>
          <Recommend />
          <Writer />
        </HomeRight>
        {this.props.showScroll ? (
          <Backtop onClick={this.handletop}>回到顶部</Backtop>
        ) : null}
      </HomeWrapper>
    );
  }
  componentDidMount() {
    this.props.changehomedata();
    this.bindEvent();
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.props.changeScolltopshow);
  }
  bindEvent() {
    window.addEventListener("scroll", this.props.changeScolltopshow);
  }
}
const mapState = state => ({
  showScroll: state.getIn(["home", "showScroll"])
});
const mapdispath = dispatch => ({
  changehomedata() {
    dispatch(actionCreators.getHomeInfo());
  },
  changeScolltopshow() {
    if (document.documentElement.scrollTop > 200) {
      dispatch(actionCreators.toggleTopshow(true));
    } else {
      dispatch(actionCreators.toggleTopshow(false));
    }
  }
});
export default connect(mapState, mapdispath)(Home);
