import * as actionType from "./actiontype";
import { fromJS } from "immutable";
import axios from "axios";
export const searchinputfocus = () => ({
  type: actionType.search_focus
});
export const searchinputblur = () => ({
  type: actionType.search_blur
});
//使用redux-thunk，让action可以返回一个函数
export const getList = () => {
  return dispatch => {
    axios
      .get("https://chenwenkai8023ni.github.io/react/api/headerList.json")
      .then(res => {
        const data = res.data;
        dispatch(change_list(data.data));
      })
      .catch(res => {
        console.log("错误");
      });
  };
};
export const mouseEnter = () => ({
  type: actionType.mouseEnter
});
export const mouseLeave = () => ({
  type: actionType.mouseLeave
});
export const Changepage = (page) => ({
  type: actionType.Changepage,
  page
});
const change_list = data => ({
  type: actionType.change_list,
  data: fromJS(data),
  //页码数
  totalpage: Math.ceil(data.length / 6)
});
