import React, { Component } from "react";
import { Link } from "react-router-dom";
//使用connect方法 让组件和store进行连接
import { connect } from "react-redux";
import { CSSTransition } from "react-transition-group";
import { ActionCreators } from "./store";
import { actionCreators as loginActionCreate } from "../../pages/login/store";
import {
  HeaderWrapper,
  HeaderNav,
  SearchWrapper,
  Logo,
  Nav,
  NavItem,
  NavSearch,
  Addition,
  Btn,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoSwitch,
  SearchInfoItem
} from "./style";

class Header extends Component {
  getListArea() {
    const {
      focused,
      list,
      page,
      mouseIn,
      totalpage,
      handleMouseEnter,
      handleMouseLeave,
      handleChangepage
    } = this.props;
    const newlist = list.toJS();
    const pagelist = [];
    if (newlist.length) {
      for (let i = (page - 1) * 6; i < page * 6; i++) {
        pagelist.push(
          <SearchInfoItem key={newlist[i].name}>
            {newlist[i].name}
          </SearchInfoItem>
        );
      }
    }
    if (focused || mouseIn) {
      return (
        <SearchInfo
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <SearchInfoTitle>
            热门搜索
            <SearchInfoSwitch
              onClick={() => {
                handleChangepage(page, totalpage, this.spinIcon);
              }}
            >
              <span
                ref={icon => {
                  this.spinIcon = icon;
                }}
                className="iconfont"
              >
                &#xe66d;
              </span>
              换一换
            </SearchInfoSwitch>
          </SearchInfoTitle>
          <div>{pagelist}</div>
        </SearchInfo>
      );
    } else {
      return null;
    }
  }
  render() {
    const {
      loginOut,
      focused,
      handlefocus,
      handleBlur,
      list,
      login,
      name
    } = this.props;
    return (
      <HeaderWrapper>
        <HeaderNav>
          <Link to="/">
            <Logo />
          </Link>
          <Nav>
            <NavItem className="left action">首页</NavItem>
            <NavItem className="left">下载App</NavItem>
            <SearchWrapper>
              <CSSTransition in={focused} timeout={200} classNames="slide">
                <NavSearch
                  className={focused ? "focused" : ""}
                  onFocus={() => handlefocus(list)}
                  onBlur={handleBlur}
                />
              </CSSTransition>
              <span
                className={focused ? "focused iconfont zoom" : "iconfont zoom"}
              >
                &#xe658;
              </span>
              {this.getListArea()}
            </SearchWrapper>
            {login ? (
              <NavItem onClick={loginOut} className="right">
                {name}
              </NavItem>
            ) : (
              <Link to="/login">
                <NavItem className="right">登录</NavItem>
              </Link>
            )}

            <NavItem className="right">
              <span className="iconfont">&#xe636;</span>
            </NavItem>
          </Nav>
          <Addition>
            <Btn className="reg">注册</Btn>
            <Link to="/write">
         
            <Btn className="write">
              <span className="iconfont">&#xe6e5;</span>
              写文章
            </Btn>
            </Link>
          </Addition>
        </HeaderNav>
      </HeaderWrapper>
    );
  }
}

//无状态组件
// const Header = this.props => {};

//组件与store连接   state表示store里的所有数据
const mapStateToprops = state => {
  return {
    //映射 将store里的focused映射给组件props
    //immutable对象调用focused必须使用get方法
    // focused: state.header.focused错误
    // focused: state.get("header").get("focused")
    focused: state.getIn(["header", "focused"]),
    list: state.getIn(["header", "list"]),
    page: state.getIn(["header", "page"]),
    totalpage: state.getIn(["header", "totalpage"]),
    mouseIn: state.getIn(["header", "mouseIn"]),
    login: state.getIn(["login", "login"]),
    name: state.getIn(["login", "name"])
  };
};
//组件改变store数据 必须调用dispath方法
const mapDispathToProps = dispath => {
  return {
    handlefocus(list) {
      //将action提交给reducer.js
      if (list.size === 0) {
        dispath(ActionCreators.getList());
      }
      dispath(ActionCreators.searchinputfocus());
    },
    handleBlur() {
      dispath(ActionCreators.searchinputblur());
    },
    handleMouseEnter() {
      dispath(ActionCreators.mouseEnter());
    },
    handleMouseLeave() {
      dispath(ActionCreators.mouseLeave());
    },
    handleChangepage(page, totalpage, spin) {
      //截取数字部分
      let orgin = spin.style.transform.replace(/[^0-9]/gi, "");
      if (orgin) {
        orgin = parseInt(orgin, 10);
      } else {
        orgin = 0;
      }
      spin.style.transform = "rotate(" + (orgin + 360) + "deg)";
      if (page < totalpage) {
        dispath(ActionCreators.Changepage(page + 1));
      } else {
        dispath(ActionCreators.Changepage(1));
      }
    },
    loginOut() {
      dispath(loginActionCreate.loginOut());
      alert("退出成功");
    }
  };
};
export default connect(mapStateToprops, mapDispathToProps)(Header);
